import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import TopNav from "./topnav"
import logo from "../images/favicon.png"

const Header = ({ siteTitle, menu }) => (
  <header
    style={{
      textcolor: `#2222ff`,
      background: `#ffffff`,
      maxWidth: 960,
      marginBottom: `0.45rem`,
    }}
  >
    <div
      style={{
        margin: `0 auto`,
        maxWidth: 960,
        padding: `0.45rem 1.0875rem`,
      }}
    >
      <h1 style={{ margin: 0 }}>
        <Link
          to="/"
          style={{
            color: `#2222ff`,
            textDecoration: `none`,
          }}
        >
        <img style={{ padding: `0em 1em`, verticalAlign: `middle`}} src={logo} width="75" height="75" alt="Haskell CS 421 Logo" />
         <span style={{verticalAlign: `middle`}}>
          { siteTitle }
        </span>
        </Link>
      </h1>
        <TopNav menuLinks={menu}/>
    </div>
  </header>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
